// Custom.scss
// Option B: Include parts of Bootstrap
@charset 'utf-8';

// 1. Include functions first (so you can manipulate colors, SVGs, calc, etc)
@import 'node_modules/bootstrap/scss/functions';

// 2. Include any default variable overrides here
$font-family-base: 'Raleway';

/**
  * This website template uses a combination of colors from the website:
  * https://coolors.co/palette/5a94c5-b4cdf6-ee4141-2b2d42-f8f32b
  */
$primary: #5A94C5;
$secondary: #B4CDF6;
$info: #F8F32B;
$danger: #EE4141;
$dark: #2B2D42;

// 3. Include remainder of required Bootstrap stylesheets
@import 'node_modules/bootstrap/scss/variables';
@import 'node_modules/bootstrap/scss/mixins';
@import 'node_modules/bootstrap/scss/root';

// 4. Include any optional Bootstrap CSS as needed
@import 'node_modules/bootstrap/scss/containers';
@import 'node_modules/bootstrap/scss/utilities';
@import 'node_modules/bootstrap/scss/spinners';
@import 'node_modules/bootstrap/scss/helpers';
@import 'node_modules/bootstrap/scss/navbar';
@import 'node_modules/bootstrap/scss/nav';
@import 'node_modules/bootstrap/scss/forms';
@import 'node_modules/bootstrap/scss/buttons';
@import 'node_modules/bootstrap/scss/transitions';
@import 'node_modules/bootstrap/scss/button-group';
@import 'node_modules/bootstrap/scss/type';
@import 'node_modules/bootstrap/scss/reboot';
@import 'node_modules/bootstrap/scss/tables';
@import 'node_modules/bootstrap/scss/grid';
@import 'node_modules/bootstrap/scss/list-group';
@import 'node_modules/bootstrap/scss/toasts';
@import 'node_modules/bootstrap/scss/close';

// 5. Optionally include utilities API last to generate classes based on the Sass map in `_utilities.scss`
@import 'node_modules/bootstrap/scss/utilities/api';

// 6. Add additional custom code here
@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

$navbar-height: 10vh;
$navbar-sticky-top-z-index: $zindex-sticky;

// Bootstrap icons
.bi {
  vertical-align: sub;
  height: 20px;
}

.LoadButton, .CatchButton, .RemoveButton, .AddButton {
  @include button-outline-variant($dark, $light, $danger, $danger);
}

.PokemonSvg {
  max-width: 500px;
  min-width: 320px;
}

.StickyTopDivision {
  top: $navbar-height !important;
  height: 100 - $navbar-height;
  z-index: -1 !important;
}
